import React from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Link, Redirect } from 'react-router-dom';

import { Button, ModalPane } from '@mssgme/ui';
import { useActions, useFetching } from '@mssgme/hooks';
import styles from './DeleteAccountPage.scss';

export function DeleteAccountPage({ backPath }) {
    const { t } = useTranslation();
    const { api } = useActions();
    const { pending, fulfilled, run: deleteAccount } = useFetching(api.users.deleteAccount, []);

    if (fulfilled) {
        return <Redirect to="/logout" />;
    }

    return (
        <ModalPane title={t('DeleteAccountPage.heading')} onClose={backPath}>
            <div className={styles.grid}>
                <p className={styles.description}>
                    {t('DeleteAccountPage.description')}
                </p>
                <Button tag={Link} to={backPath} fullWidth btnStyle="secondary2">
                    {t('DeleteAccountPage.cancel')}
                </Button>
                <Button fullWidth onClick={deleteAccount} btnStyle="danger" isFetching={pending}>
                    {t('DeleteAccountPage.delete')}
                </Button>
            </div>
        </ModalPane>
    );
}

DeleteAccountPage.propTypes = {
    backPath: PropTypes.string.isRequired,
};
